J,CA+HI,yBALF,kBAMI,cA5HJ,CACF","sourcesContent":["\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import '@/scss/vars.scss';\n.contact-form {\n  display: flex;\n  flex-wrap: wrap;\n  $root: &;\n\n  &--footer {\n\n    @media(max-width: 991px) {\n      justify-content: center;\n    }\n\n    #{$root} {\n      &__form-group {\n        width: 100%;\n        max-width: 33.333%;\n        padding: 0 15px;\n        margin-bottom: 0;\n\n        @media(max-width: 991px) {\n          max-width: 50%;\n        }\n\n        @media(max-width: 767px) {\n          max-width: 100%;\n          padding: 0;\n          padding-bottom: 20px;\n        }\n      }\n      &__btn {\n        width: 100%;\n        max-width: calc(33.333% - 30px);\n        margin: 0 15px;\n\n        @media(max-width: 991px) {\n          margin: 20px 0 0;\n          max-width: calc(100% - 30px);\n        }\n\n        @media(max-width: 767px) {\n          margin: 0;\n          max-width: 100%;\n        }\n      }\n    }\n  }\n\n  &__form-group {\n    width: 100%;\n    position: relative;\n    margin-bottom: 20px;\n  }\n\n  &__error {\n    color: $red;\n    font-size: 12px;\n    line-height: 21px;\n    margin-top: 3px;\n    display: block;\n\n    @media (min-width: 768px) and (max-width: 992px) {\n      font-size: 10px;\n    }\n  }\n\n  &__form-input {\n    font-size: 16px;\n    color: $primary;\n    border: 2px solid $borderColor;\n    background: transparent;\n    box-shadow: none;\n    border-radius: 6px;\n    padding: 0 20px;\n    width: 100%;\n    height: 51px;\n    outline: none;\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    transition: all 0.18s linear;\n\n    &:hover, &:focus {\n      color: $primary;\n      border-color: transparent;\n      background: $borderColor;\n      box-shadow: none;\n    }\n\n    @media(max-width: 767px) {\n      padding: 0 10px;\n      font-size: 14px;\n    }\n  }\n\n  &__btn {\n    font-weight: 600;\n    font-size: 15px;\n    padding: 0 20px;\n    height: 51px;\n    transition: background 0.3s, color 0.3s;\n    color: #fff;\n    border: none;\n    background: $yellow;\n    border-radius: 6px;\n\n    @media(max-width: 767px) {\n      font-size: 14px;\n      padding: 12px 20px;\n    }\n\n    &--offset {\n      @media (min-width: 768px) and (max-width: 1199px) {\n        margin-bottom: 23px !important;\n      }\n    }\n\n    &:hover, &:focus {\n      background: darken($color: $yellow, $amount: 10%);\n    }\n  }\n\n  &__status {\n    max-width: 100%;\n    width: 100%;\n    text-align: center;\n    font-size: 14px;\n    font-weight: 500;\n    color: $red;\n    margin-top: 10px;\n\n    @media(max-width: 767px) {\n      margin-top: 5px;\n      font-size: 12px;\n    }\n  }\n}\n.success {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  padding: 60px 0;\n\n  @media(max-width: 767px) {\n    padding: 40px 0;\n  }\n\n  &__ico {\n    width: 105px;\n    height: 105px;\n    margin-bottom: 20px;\n\n    @media(max-width: 767px) {\n      width: 90px;\n      height: 90px;\n    }\n  }\n\n  &__message {\n    text-align: center;\n    font-size: 20px;\n    font-weight: 600;\n    color: $primary;\n    @media(max-width: 767px) {\n      font-size: 18px;\n    }\n  }\n\n}\n","$primary: #484848;\n$secondary: #a2a1a1;\n$blue: #a993dc;\n$red: #ef726b;\n$green: #7dca3f;\n$brown: #d88861;\n$yellow: #ffad0d;\n$shadow: rgb(0, 0, 0, .10);\n$borderColor: #e6e6e6;\n\n@mixin placeholder($color) {\n  &::-moz-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::-webkit-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &:-ms-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::-ms-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::placeholder {\n    color: $color;\n    opacity: 1;\n  }\n}"],"sourceRoot":""}]);
